
#model {
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  margin: auto;
}
#model #dialog {
  width: 300px;
  height: 300px;
  border: 1px dashed #008080;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  margin: auto;
  background: #fff;
  text-align: center;
}
#model #dialog .close {
  width: 8%;
  position: absolute;
  right: 2%;
  top: 2%;
  transition: all 1s;
}
#model #dialog .close:hover {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
#model #dialog h1 {
  font: 3rem/4rem "微软雅黑";
  color: #fff;
  background: #008080;
}
#model #dialog p {
  font: 1.6rem/3.6rem "微软雅黑";
  color: #fff;
  background: #008080;
}
#model #dialog input {
  display: block;
  width: 90%;
  height: 30px;
  margin: 2% auto;
  padding-left: 2%;
}
#model #dialog button {
  width: 94%;
  background: #008080;
  display: block;
  margin: 2% auto;
  height: 35px;
  border: none;
  font-size: 1.6rem;
  letter-spacing: 2px;
  color: #fff;
}


