
.share_model {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  z-index: 80;
}
.share_model .qr_code {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -50px;
}
.share_model .qr_code img {
  width: 50%;
}
.share_model .qr_code p {
  text-align: left;
  color: #fff;
  font: 1.6rem/2rem "华文细黑";
}


